.container {
  margin: 50px 0;
}

.card {
  margin: 0 2% !important;
}

.infected {
  border-bottom: 10px solid rgba(255, 52, 52, 0.774);
}

.recovered {
  border-bottom: 10px solid rgba(107, 252, 102, 0.911);
}

.deaths {
  border-bottom: 10px solid rgba(43, 43, 241, 0.932);
}

@media (max-width: 770px) {
  .card {
    margin: 2% 1% !important;
  }
}
